const $ = require('jquery');
import axios from 'axios';
const comm={
    //axios VUE ajax 
    ajax(obj) {
        axios({
            url: obj.url,
            method: obj.method || 'get',
            data: obj.data,
            transformRequest: [function (data) {
                if (obj.paramJson) {
                    return 'paramJson=' + JSON.stringify(data)
                }
            }],
            headers: {
                'X-Requested-With': 'XMLHttpRequest'
            }
        })
            .then(function (res) {
                obj.success && obj.success(res)
            }).catch(function (err) {
            obj.fail && obj.fail(err)
        })
    },
    //ajax
    ajax2(options) {
        options = options || {}
        options.type = (options.type || 'GET').toUpperCase()
        options.dataType = options.dataType || 'json'
        let params = formatParams(options.data)
        let xhr = new XMLHttpRequest()
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4) {
                let status = xhr.status
                if (status >= 200 && status < 300) {
                    options.success && options.success(JSON.parse(xhr.responseText), xhr.responseXML)
                } else {
                    options.fail && options.fail(status)
                }
            }
        }
        //   - 
        if (options.type == 'GET') {
            xhr.open('GET', options.url + '?' + params, true)
            xhr.send(null)
        } else if (options.type == 'POST') {
            xhr.open('POST', options.url, true)
            //
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded')
            xhr.send(params)
        }

        //
        function formatParams (data) {
            let arr = []
            for (let name in data) {
                arr.push(encodeURIComponent(name) + '=' + encodeURIComponent(data[name]))
            }
            arr.push(('v=' + Math.random()).replace('.', ''))
            return arr.join('&')
        }

    },
    //:   
    cutLine(srcStr,cutType,type,callType){
        let oneStep=[];
        if(srcStr&&srcStr.lastIndexOf(cutType)>-1){
            oneStep = srcStr.split(cutType);
        }else{
            oneStep.push(srcStr);
        }
        let str="";

        for(let i=0;i<oneStep.length;i++){
            if(oneStep[i]){
                if(oneStep[i].split(type)[1]){
                    str+= oneStep[i].split(type)[1]+callType;
                }else{
                    str+= oneStep[i]+callType;
                }
            }
        }
        return str.substring(0,str.length-1);
    },
    /*
    * 
    *  getStrByteLen(,) //
    * */
    getStrLen(str,len) {
        let newStr='',newLength=0;
        if(!str){//
            return '';
        }
        for(let i=0;i<str.length;i++){
            if(str.charCodeAt(i)>128){
                newLength+=2;
            }else{
                newLength++;
            }
            if(newLength<=len){
                newStr=newStr.concat(str[i]);
            }else{
                break;
            }
        }
        if(newLength>len){
            newStr = newStr+"..."
        }
        return newStr;
    },
    //  
    getByteLen(str){
        let newLength=0;
        if(!str){//
            return 0;
        }
        for(let i=0;i<str.length;i++){
            if(str.charCodeAt(i)>128){
                newLength+=2;
            }else{
                newLength++;
            }
        }
        return newLength;
    },
    getParamFromUrl(url, name){
        const str = url.split('?');
        const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
        const r = str[1].match(reg);
        if (r != null) {
            return unescape(r[2]);
        }
        return null;
    },
    //
    getpara(symbol){
        let t = this;
        let url=document.URL;
        let param={};
        let str,item;
        if(url.lastIndexOf(symbol?symbol:"?")>0) {
            str=url.substring(url.lastIndexOf(symbol?symbol:"?")+1,url.length);
            let arr=str.split("&");
            for(let i=0;i<arr.length;i++) {
                item =  arr[i].split("=");
                param[item[0]] = t.htmlToString(decodeURIComponent(item[1]));
            }
        }
        return param;
    },
    //
    isEmptyObject(obj){
        for(let n in obj){return false}
        return true;
    },
    //responseData
    hasResponseData(r) {
        if (r && r.responseObject && r.responseObject.responseData && !this.isEmptyObject(r.responseObject.responseData)) {
            return true
        }
        return false
    },
    //responseData
    hasResponseData2(r) {
        if (r  && r.responseData && !this.isEmptyObject(r.responseData)) {
            return true
        }
        return false
    },
    //responseMessage
    hasResponseMessage(r) {
        if (r && r.responseObject && r.responseObject.responseMessage && !this.isEmptyObject(r.responseObject.responseMessage)) {
            return true
        }
        return false
    },
    //html
    htmlToString(str){
        let rstStr = (str+ '').replace(/[<>&]/g,function(c){return {'<':'&lt;','>':'&gt;','&':'&amp;'}[c];});
        let tempArr = rstStr.split("\&lt\;\/a\&gt\;&lt\;a");
        if(tempArr.length>=2){
            rstStr = tempArr.map(function(d,index) {
                let s =  d.replace(/\&lt\;a[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)/gi,"<a href=\"$1\">$2");
                s =  s.replace(/[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)&lt\;\/a\&gt\;/gi,"<a href=\"$1\">$2</a>");
                s =  s.replace(/[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)/gi,"<a href=\"$1\">$2</a>");
                return s;
            }).join();
        }else{
            rstStr = (rstStr+'').replace(/\&lt\;a[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)\&lt\;\/a\&gt\;/gi,"<a href=\"$1\">$2</a>"); /* A*/
        }
        rstStr = rstStr.replace(/@@/g,"@");
        return rstStr;
    },
    //xss
    symbolToString(str){
        let rstStr = str.replace(/[<>]/g,function(c){return {'<':'&lt;','>':'&gt;'}[c];});
        let tempArr = rstStr.split("\&lt\;\/a\&gt\;&lt\;a");
        if(tempArr.length>=2){
            rstStr = tempArr.map(function(d,index) {
                let s =  d.replace(/\&lt\;a[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)/gi,"<a href=\"$1\">$2");
                s =  s.replace(/[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)&lt\;\/a\&gt\;/gi,"<a href=\"$1\">$2</a>");
                s =  s.replace(/[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)/gi,"<a href=\"$1\">$2</a>");
                return s;
            }).join();
        }else{
            rstStr = (rstStr+'').replace(/\&lt\;a[\s]*href\=\"?(\S*)\"?\&gt\;([\S\s]*)\&lt\;\/a\&gt\;/gi,"<a href=\"$1\">$2</a>"); //A
        }
        rstStr = rstStr.replace(/@@/g,"@");
        return rstStr;
    },
    //
    escapeReplace(str){
        return str.replace(/[+/?#&=!%]/g,function(c){return {'+':'%2B','/':'%2F','?':'%3F','#':'%23','&':'%26','=':'%3D','!':'%21','%':'%25'}[c];});
    },
    bodyScroll(){//
        document.getElementsByTagName("body")[0].style.height="100%";
        document.getElementsByTagName("html")[0].style.height="100%";
        document.getElementsByTagName("body")[0].style.overflow="hidden";
        document.getElementsByTagName("html")[0].style.overflow="hidden";
    },
    bodyNoScroll(){//
        document.getElementsByTagName("body")[0].style.height="auto";
        document.getElementsByTagName("html")[0].style.height="auto";
        document.getElementsByTagName("body")[0].style.overflow="auto";
        document.getElementsByTagName("html")[0].style.overflow="auto";
    },
    //
    browser :{
        mozilla : /firefox/.test(navigator.userAgent.toLowerCase()),
        webkit : /webkit/.test(navigator.userAgent.toLowerCase()),
        opera : /opera/.test(navigator.userAgent.toLowerCase()),
        msie : /msie/.test(navigator.userAgent.toLowerCase()),
        versions: function () {
            let u = navigator.userAgent, app = navigator.appVersion;
            return {//
                trident: u.indexOf('Trident') > -1, //IE
                presto: u.indexOf('Presto') > -1, //opera
                webKit: u.indexOf('AppleWebKit') > -1, //
                gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
                mobile: !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/), //
                ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
                android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
                iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1, //iPhoneQQHD
                iPad: u.indexOf('iPad') > -1, //iPad
                webApp: u.indexOf('Safari') === -1 //web
            };
        }(),
        language: (navigator.browserLanguage || navigator.language).toLowerCase(),
        isIe8:function(){
            let browser = navigator.appName;
            let b_version = navigator.appVersion;
            let version = b_version.split(";");
            let trim_Version = (version[1]!=undefined&&version[1]!="")?version[1].replace(/[ ]/g, ""):"";
            if (browser === "Microsoft Internet Explorer" && trim_Version=="MSIE8.0") {
                return true;
            } else {
                return false;
            }
        },
        isIe9:function(){
            let browser = navigator.appName;
            let b_version = navigator.appVersion;
            let version = b_version.split(";");
            let trim_Version = (version[1]!=undefined&&version[1]!="")?version[1].replace(/[ ]/g, ""):"";
            if (browser === "Microsoft Internet Explorer" && trim_Version=="MSIE9.0") {
                return true;
            } else {
                return false;
            }
        },
        isIeOrEdge:function() {
            let userAgent = navigator.userAgent; //userAgent
            let isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1; //IE<11
            let isEdge = userAgent.indexOf("Edge") > -1 && !isIE; //IEEdge
            let isIE11 = userAgent.indexOf('Trident') > -1 && userAgent.indexOf("rv:11.0") > -1;
            if(isIE) {
                let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
                reIE.test(userAgent);
                let fIEVersion = parseFloat(RegExp["$1"]);
                if(fIEVersion == 7) {
                    return true;
                } else if(fIEVersion == 8) {
                    return true;
                } else if(fIEVersion == 9) {
                    return true;
                } else if(fIEVersion == 10) {
                    return true;
                } else {
                    return true;//IE<=7
                }
            } else if(isEdge) {
                return true;//edge
            } else if(isIE11) {
                return true; //IE11
            }else{
                return false;//ie
            }
        },
        isLessIe10:function() {
            let userAgent = navigator.userAgent; //userAgent
            let isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1; //IE<11
            let isEdge = userAgent.indexOf("Edge") > -1 && !isIE; //IEEdge
            let isIE11 = userAgent.indexOf('Trident') > -1 && userAgent.indexOf("rv:11.0") > -1;
            if(isIE) {
                let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
                reIE.test(userAgent);
                let fIEVersion = parseFloat(RegExp["$1"]);
                if(fIEVersion == 7) {
                    return true;
                } else if(fIEVersion == 8) {
                    return true;
                } else if(fIEVersion == 9) {
                    return true;
                } else if(fIEVersion == 10) {
                    return true;
                } else {
                    return false;//IE<=7
                }
            } else if(isEdge) {
                return false;//edge
            } else if(isIE11) {
                return false; //IE11
            }else{
                return false;//ie
            }
        },
    },
    //cookie
    cookie : {
        set(key,val,time){//cookie
            let date=new Date(); //
            let expiresDays=time;  //daten
            date.setTime(date.getTime()+expiresDays*24*3600*1000); //cookie
            document.cookie=key + "=" + val +";expires="+date+";domain=.allinmd.cn";  //cookie
        },
        get(key){//cookie
            /*cookie*/
            let getCookie = document.cookie.replace(/[ ]/g,"");  //cookiecookie
            let arrCookie = getCookie.split(";");  //cookie"" cookiearrCookie
            let tips;  //tips
            for(let i=0;i<arrCookie.length;i++) {   //forcookietips
                let arr = arrCookie[i].split("=");   //cookie""cookiearr
                if (key == arr[0]) {  //arr[0]cookietips
                    tips = arr[1];   //cookietips
                    break;   //for
                }
            }
            return tips;
        },
        delete(key) { //cookie
            if(this.get(key)){
                // document.cookie = key + "=; expires =-1;domain=.allinmd.cn;";//cookie
                this.set(key,'',-1)
            }
        }
    },
    //
    checkUser(){
        let userId = this.cookie.get("userId");
        let localUserId = localStorage.getItem("userId");
        if(!userId||userId==""){// ID
            window.location.href="//www.allinmd.cn?from=emr"
        }else if(userId!=localUserId){//cookieID
            location.reload();
        }
    }
};

export default comm;
